  // import { apiOrderDetail, apiOrderSubmit, apiOrderFee, apiUserInfo, apiCouponList } from '@/service/index'
	export default {
		data() {
			return {
				tabIndex: 0,
				id: '',
				status: 1,
				shopNum: 1,
				shopList: [],
				addressItem: {},
				remark: '',
				mobile: '',
				cardId: [],
				total: '0.00',
				orderAmout: 0,
				delivery_fee: '',
				detail: {},
				couponId: '',
				couponText: '',
				couponItem: {},
				isCoupon: false
			}
		},
		onLoad(options) {
      this.shopList = uni.getStorageSync('shopList');
      // this.shopList.forEach((item) => {
      // 	if(item.cart_id){
      //     this.cardId.push(item.cart_id)
      // 	}else {
      //     this.getDetail(item)
      // 	}
      // })
      // this.getOrderFee()
      // this.getUserInfo()
      // uni.removeStorageSync('addressItem');
		},
		onShow() {
      const value = uni.getStorageSync('addressItem');
      if(value){
      	this.addressItem = uni.getStorageSync('addressItem');
      }
		},
		methods: {
      selectTab(index) {
        this.tabIndex = index
        this.getTotalFee()
      },
      getUserInfo() {
        apiUserInfo()
        .then((res) => {
          if(res.code == 0) {
            this.detail = res.data
          }
        })
      },
      getOrderFee() {
        apiOrderFee()
        .then((res) => {
          if(res.code == 0) {
            this.delivery_fee = res.data.delivery_fee
            this.getTotalFee()
          }
        })
      },
			getTotalFee() {
        let total = 0
        this.shopList.forEach((item) => {
        	total = total + item.shopNum * parseFloat(item.goods_price)
        })
        if(this.tabIndex == 1) {
          total = total + parseFloat(this.delivery_fee)
        }
        this.total = total.toFixed(2)
        this.orderAmout = this.total
			},
			getDetail(item) {
        apiOrderDetail({
        	goods_id: item.goods_id,
        	buy_number: item.shopNum
        })
        .then((res) => {
        	if(res.code == 0) {
            this.cardId.push(res.data.cart_id)
        	}
        })
			},
			addressPage() {
        uni.navigateTo({
          url: '/pages/address/address?origin=2'
        });
			},
			onSubmit() {
        if(this.tabIndex == 0 && !this.mobile) {
          uni.showToast({
            title: '自提请填写手机号码',
            icon: 'none',
            duration: 2000
          });
          return
        }
				if(this.tabIndex == 1 && !this.addressItem.address_id) {
          uni.showToast({
            title: '请选择收货地址',
            icon: 'none',
            duration: 2000
          });
          return
				}
        let params = {
          cart_id: this.cardId.join(','),
          remark: this.remark,
          take_fashion: parseInt(this.tabIndex) + 1
        }
        if(this.tabIndex == 0) {
          params.take_mobile = this.mobile
        }
        if(this.tabIndex == 1) {
          params.address_id = this.addressItem.address_id
        }
				apiOrderSubmit(params)
				.then((res) => {
					if(res.code == 0) {
						const self = this
            if(!res.data) {
              self.successPage()
              return
            }
            uni.requestPayment({
                provider: 'wxpay',
                timeStamp: res.data.timeStamp,
                nonceStr: res.data.nonceStr,
                package: res.data.package,
                signType: 'MD5',
                paySign: res.data.paySign,
                success: function (res) {
                  self.successPage()
                },
                fail: function (err) {
                  uni.showToast({
                    title: '支付失败',
                    icon: 'none',
                    duration: 2000
                  });
                }
            });
					}
				})
			},
			successPage() {
				uni.navigateTo({
				  url: '/pages/success/success'
				});
			}
		}
	}
